<template>
  <div id="main">
      <nav><a>Waiting首页</a><span>&nbsp;>&nbsp;</span><a>宠物领养</a><span>&nbsp;>&nbsp;</span><a>{{petclass}}</a><span>&nbsp;>&nbsp;</span><a>内容详情</a></nav>
      <div class="detail">
          <h1>{{intro}}</h1>
          <h4>由用户:<span>{{master}}</span>发表于<span>{{create_time.slice(0,10)}}/{{create_time.slice(11,16)}}</span></h4>
          <h4><span>宠物名:{{pname}}</span>来自:{{city}} </h4>
          <div class="img"></div>
          <div>
            <img src="" alt="">
          </div>
          <p><span>&nbsp;&nbsp;&nbsp;</span>{{details}}</p>
      </div>
  </div>
</template>
<script>
  export default{
    data(){
      return{
        ser_id:sessionStorage.getItem("ser_id"),
        petclass:"",
        intro:"",
        master:"",
        create_time:"",
        city:"",
        pname:"",
        details:""
      }
    },
    created() {
      this.axios.get("http://127.0.0.1:3000/pet/petDetails",{params:{ser_id:this.ser_id}}).then(res=>{
        var data = res.data[0]
        console.log(data);
        this.intro = data.intro;
        var petclass=res.data[0].ser_id.slice(0,1);
          if(petclass=="D")
            this.petclass="狗狗"
          else if(petclass=="C")
            this.petclass="小猫"
          else
            this.petclass="其他宠物"
        this.create_time = data.create_time 
        this.city=data.city  
        this.pname=data.pname
        this.details=data.details
        this.master=data.master
        console.log(data.master);
      }).then(()=>{
        var master = this.master
          this.axios.get("http://127.0.0.1:3000/pet/master",{params:{master}}).then(result=>{
            this.master=result.data
            console.log(this.master);
          })
      })
    },
    mounted() {
      
    },
  }
</script>
<style scoped>
 #main{
    margin: 0 auto;
    margin-top:70px; 
    width: 1200px;
    border: 1px solid transparent;
    box-shadow: 0 0 4px rgba(250,136,83,0.8)
  }
  #main>nav{
    width: 90%;
    height: 40px;
    text-align: left;
    line-height: 40px;
    margin:10px 60px;
    margin-bottom:0px;  
    border-bottom:1px solid #e6e6e6;
  }
  #main>nav a:hover{
    text-decoration: underline;
    cursor: pointer;
  }
  .detail{
    width: 80%;
    height: 800px;
    border: 1px dashed #aaa;
    margin: 10px auto;
  }
</style>